.navbar {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: var(--dark-blue-clr);
  height: 50px;
  position: sticky;
  top: 0;
  z-index: 1;
  vertical-align: middle;
  transition: var(--transition);

  .nav-container {
    position: relative;
    max-width: 1140px;
    margin: auto;
    display: flex;
    align-items: center;
    align-content: center;
    height: 100%;
  }
  .nav-row {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    white-space: nowrap;
  }

  .nav-logo {
    padding: 0 0 0 1rem;
  }
  .delomid-logo {
    width: 10.5rem;
    height: 50px;
    object-fit: cover;
  }
  .delomid-logo-smaller {
    width: 10.5rem;
    height: 50px;
    object-fit: cover;
    transition: var(--transition);
  }
  img {
    transition: var(--transition);
  }
  img:hover {
    opacity: 0.7;
  }
  .nav-links {
    display: none;
  }
  .nav-link {
    padding: 10px 0;
    font-size: var(--f-lg);
    color: #edf0f2;
    transition: var(--transition);
    position: relative;
    z-index: 2;
  }
  .nav-link:hover {
    color: var(--red-clr);
  }

  .close {
    height: 0;
    width: 0;
    overflow: hidden;
  }

  .nav-toggle article {
    position: relative;
  }

  .menu-icon {
    position: absolute;
    z-index: 2;
    background-color: var(--dark-blue-clr);
    transition: var(--transition);
  }

  .menu-icon-close {
    position: relative;
    z-index: 1;
    background-color: var(--dark-blue-clr);
    transition: var(--transition);
    transform: rotate(90deg);
  }
  .transform {
    opacity: 0;
    z-index: 0;
    background-color: transparent;
    transform: rotate(360deg);
  }
}

.nav-search {
  position: relative;
  color: var(--light-clr);
  font-size: 1.5rem;
  transition: var(--transition);
  padding-right: 1rem;
}
.nav-toggle {
  display: inline-block;
  color: var(--light-clr);
  font-size: 1.5rem;
  padding-right: 10px;
  cursor: pointer;
  transition: var(--transition);
}
.nav-search:hover,
.nav-toggle:hover {
  color: var(--red-clr);
  cursor: pointer;
}

.show-dropdown {
  li a {
    font-size: 16px !important;
    transition: var(--transition);
  }

  li a:hover {
    color: var(--red-clr);
  }
  position: absolute;
  background-color: var(--dark-blue-clr);
  opacity: 1;
  transition: var(--transition);
  max-height: 100px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 20px;
  font-size: 18px;
  margin-left: -20px;
}

.hide-dropdown {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: absolute;
  transition: var(--transition);
  max-height: 0;
  overflow: hidden;
    margin-left: 0;


}
.dropdown-link {
  font-size: 18px;
}

@media screen and (min-width: 599px) {
  .nav-smaller {
    height: 60px !important;
  }
  .navbar {
    height: 80px;

    .delomid-logo {
      width: 12rem;
      height: 80px;
    }
  }
  .nav-toggle {
    padding: 0;
  }
}
@media screen and (max-width: 922px) {
  .navbar {
    .close {
      width: auto !important;
      height: auto;
    }
  }
}

@media screen and (min-width: 922px) {
  .nav-smaller {
    height: 100px !important;
  }

  .navbar {
    height: 103px;
    padding: 0 2rem;
    .nav-link {
      font-size: 18px;
      overflow: hidden;
    }

    .nav-toggle {
      display: none;
    }
    .delomid-logo {
      width: 14rem;
      height: 100px;
    }
    .delomid-logo-smaller {
      width: 14rem;
      height: 90px;
      transform: scale(0.85);
      object-fit: cover;
      transition: var(--transition);
    }
    .nav-links {
      display: flex;
      justify-content: end;
      gap: 2rem;
      font-weight: 400;
      font-size: 1.2rem;
    }
  }
}
